import React, { Component } from 'react';
import { CardList } from './components/card-list/card-list';
import SearchBox from './components/search-box/search';
import './App.css';
class App extends Component {
	constructor () {
		super();
		this.state = {
			monster      : [],
			searchField  : '',
			monsterClone : []
		};
	}
	componentDidMount () {
		fetch('https://jsonplaceholder.typicode.com/users')
			.then((res) => res.json())
			.then((resJson) => this.setState({ monster: resJson, monsterClone: resJson }));
	}

	del = (id) => {
		const newMonsters = this.state.monster.filter((monster) => monster.id !== id);
		this.setState({ monster: newMonsters });
	};
	render () {
		const { monster, searchField } = this.state;
		const filteredMonster = monster.filter((monster) => monster.name.toLowerCase().includes(searchField));
		return (
			<div className='App'>
				<h1> Monsters </h1>
				<SearchBox
					placeholder={'Search Monsters'}
					handleChange={(e) => this.setState({ searchField: e.target.value })}
				/>
				<CardList monster={filteredMonster} del={this.del} />
			</div>
		);
	}
}

export default App;
